@import './Assets/sass/main.scss';

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html {
	font-size: 62.5%;
	@media only screen and (max-width: 1200px) {
		// 	font-size: 56.25%;
		font-size: 59.375%;
	}
	height: 1000rem;
	scroll-behavior: smooth;
}

body {
	font-family: 'Roboto', sans-serif;
	font-size: 1.4rem;
	color: #6f6f6f;
	font-weight: 400;
	line-height: 1.6;
	text-rendering: optimizeLegibility;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}
